import React from "react";
import { EmploymentStatus } from "src/types/resumeProfile";
import { toLocalDate, formatDigits, calculateAge } from "src/utils";

const employmentStatusMap: Record<EmploymentStatus, string> = {
    looking_for_job: "  ",
    employed: "  ",
    freelancer: "",
};

function MoreAboutMeComponent({
    birthDate,
    employmentStatus,
}: {
    birthDate?: string;
    employmentStatus: EmploymentStatus;
}) {
    return (
        <div className="w-full py-1">
            <h4 className="text-lg font-bold"> </h4>
            <ul className="m-1">
                {birthDate && (
                    <li>
                         {toLocalDate(birthDate, "short")}{" "}
                        {formatDigits(` (${calculateAge(birthDate)} )`)}
                    </li>
                )}
                <li>   {employmentStatusMap[employmentStatus]}</li>
            </ul>
        </div>
    );
}

export default MoreAboutMeComponent;
